import React, {useEffect, useState} from 'react';
import {IonButton, IonContent, IonLoading, IonPage, IonToast} from '@ionic/react';
import './Partenaire.css';
import {AppHeader} from "../../components/AppHeader/AppHeader";
import {Geolocation, Geoposition} from "@ionic-native/geolocation";
import Barcode from "../../components/BarCode/Barcode";

interface LocationError {
    showError: boolean;
    message?: string;
}

const Partenaire: React.FC = () => {
    const [loading, setLoading] = useState<boolean>(false);
    const [error, setError] = useState<LocationError>({ showError: false });
    const [position, setPosition] = useState<Geoposition>();
    const [url, setUrl] = useState('');

    useEffect( () => {
       getLocation()
    });

    const getLocation = async () => {
        if (position === undefined) {
            setLoading(true);

            try {
                const position = await Geolocation.getCurrentPosition();
                setPosition(position);
                setLoading(false);
                // @ts-ignore
                setUrl(`https://maps.google.com/maps?q=${position.coords.latitude},${position.coords.longitude}&hl=es&z=14&amp;output=embed`)
                setError({ showError: false });
            } catch (e) {
                setError({ showError: true, message: e.message });
                setLoading(false);
            }
        }
    }

    return (
        <IonPage>
            <AppHeader />
            <IonContent>
                <IonLoading
                    isOpen={loading}
                    onDidDismiss={() => setLoading(false)}
                    message={'Récupération de la localisation...'}
                />
                <IonToast
                    isOpen={error.showError}
                    onDidDismiss={() => setError({ message: "", showError: false })}
                    message={error.message}
                    duration={3000}
                />

                {console.log(position)}
                {/*<iframe*/}
                {/*    src={url} width="600" height="450" frameBorder="0" aria-hidden="false">*/}

                {/*</iframe>*/}

                <Barcode />
            </IonContent>
        </IonPage>
    );

    /*
    return (
        <>
            <IonLoading
                isOpen={loading}
                onDidDismiss={() => setLoading(false)}
                message={'Getting Location...'}
            />
            <IonToast
                isOpen={error.showError}
                onDidDismiss={() => setError({ message: "", showError: false })}
                message={error.message}
                duration={3000}
            />
            <IonButton color="primary" onClick={getLocation}>{position ? `${position.coords.latitude} ${position.coords.longitude}` : "Get Location"}</IonButton>
        </>
    );
     */
};

export default Partenaire;
